:root {
  --primary-color: #2563eb;
  --secondary-color: #1e293b;
  --text-color: #1e293b;
  --background-color: #fff;
  --accent-color: #3b82f6;
  --border-color: #e5e7eb;
  --card-background: #fff;
  --text-secondary: #9ca3af;
}

[data-theme="dark"] {
  --primary-color: #60a5fa;
  --secondary-color: #e2e8f0;
  --text-color: #f3f4f6;
  --background-color: #1e1e34;
  --accent-color: #3b82f6;
  --border-color: #94a6c3;
  --card-background: #1f2937;
  --text-secondary: #9ca3af;
}

.btn-primary {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border-radius: 8px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px #0000001a;
}

.btn-secondary {
  color: var(--text-color);
  border: 2px solid var(--text-color);
  cursor: pointer;
  background-color: #0000;
  border-radius: 8px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.btn-secondary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px #0000001a;
}

.card {
  background-color: var(--card-background);
  border-radius: 12px;
  padding: 2rem;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 4px 6px #0000001a;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 12px #00000026;
}

.social-links {
  align-items: center;
  gap: 1rem;
  display: flex;
}

.social-links a {
  color: var(--text-color);
  text-decoration: none;
  transition: color .3s;
}

.social-links a:hover {
  color: var(--primary-color);
}

header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  display: flex;
  position: relative;
}

@media screen and (width >= 1280px) {
  header .logo {
    z-index: 0;
  }
}

header nav ul {
  gap: 2rem;
  margin: 0;
  list-style: none;
  display: flex;
}

header nav ul a {
  color: var(--text-color);
  letter-spacing: .05em;
  font-size: 1.1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

header nav ul a:hover {
  color: var(--primary-color);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.grid {
  gap: 2rem;
  display: grid;
}

.grid-2 {
  grid-template-columns: repeat(2, 1fr);
}

.grid-3 {
  grid-template-columns: repeat(3, 1fr);
}

.grid-4 {
  grid-template-columns: repeat(4, 1fr);
}

@media screen and (width <= 1280px) {
  .grid-2, .grid-3, .grid-4 {
    grid-template-columns: 1fr;
  }
}

@media screen and (width >= 1280px) {
  .hero .hero-content {
    grid-template-columns: 1fr 1fr;
  }

  .hero .hero-image {
    justify-content: flex-end;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  background-color: var(--background-color);
  transition: background-color .3s;
}

body {
  color: var(--text-color);
  background-color: var(--background-color);
  min-height: 100vh;
  font-family: Inter, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  transition: background-color .3s, color .3s;
}

.hero {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.hero .hero-content {
  grid-template-columns: 1fr;
  align-items: center;
  gap: 4rem;
  display: grid;
}

@media screen and (width >= 1280px) {
  .hero .hero-content {
    grid-template-columns: 1fr 1fr;
  }
}

.hero .hero-text h1 {
  margin-bottom: 2rem;
  font-size: 3.5rem;
  line-height: 1.2;
}

.hero .hero-text p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.hero .hero-text .cta-buttons {
  gap: 1rem;
  display: flex;
}

.hero .hero-image {
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero .hero-image img {
  object-fit: contain;
  border-radius: 100px;
  width: 100%;
  max-width: 500px;
  height: auto;
}

@media screen and (width >= 1280px) {
  .hero .hero-image {
    justify-content: flex-end;
  }
}

.latest-codes {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.latest-codes h2 {
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

.latest-codes p {
  margin-bottom: 2rem;
}

.latest-codes p a {
  color: var(--primary-color);
  text-decoration: none;
  transition: color .3s;
}

.latest-codes p a:hover, [data-theme="dark"] .latest-codes p a {
  color: var(--accent-color);
}

[data-theme="dark"] .latest-codes p a:hover {
  color: var(--primary-color);
}

.latest-codes .projects-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
  display: grid;
}

.social-links {
  align-items: center;
  gap: 1rem;
  margin-left: auto;
  padding-right: 1rem;
  display: flex;
}

@media screen and (width <= 1280px) {
  .social-links {
    display: none;
  }
}

.social-links a, .social-links button {
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: transform .2s;
  display: flex;
}

.social-links a:hover, .social-links button:hover {
  transform: scale(1.1);
}

.social-links .social-icon {
  object-fit: contain;
  width: 24px;
  height: 24px;
}

[data-theme="dark"] .social-links a[href*="github"] .social-icon {
  filter: invert();
}

.social-links button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.mobile-social-links {
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding-top: 4rem;
  display: flex;
  position: absolute;
  bottom: 4rem;
  left: 0;
  right: 0;
}

.mobile-social-links a, .mobile-social-links button {
  color: var(--text-color);
  text-decoration: none;
  transition: color .3s;
}

.mobile-social-links a:hover, .mobile-social-links button:hover {
  color: var(--primary-color);
}

.mobile-social-links .social-icon {
  object-fit: contain;
  width: 24px;
  height: 24px;
}

[data-theme="dark"] .mobile-social-links a[href*="github"] .social-icon {
  filter: invert();
}

.mobile-social-links button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.project-card {
  background: var(--card-background);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
  transition: all .3s;
  display: flex;
}

.project-card h3 {
  color: var(--text-color);
  align-items: center;
  gap: 1rem;
  margin: 0;
  font-size: 2rem;
  display: flex;
}

.project-card h3:before {
  content: "";
  background-image: url("github.4e3c507c.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 24px;
  display: inline-block;
}

[data-theme="dark"] .project-card h3:before {
  background-image: url("github-light.48188536.png");
}

.project-card p {
  color: var(--text-secondary);
  margin: 0;
  font-size: .95rem;
  line-height: 1.5;
}

.project-card a {
  color: var(--text-color);
  align-items: center;
  gap: 1rem;
  margin-top: auto;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.project-card a i {
  font-size: 1.2rem;
}

.mobile-menu {
  background-color: var(--background-color);
  z-index: 999;
  text-align: center;
  -webkit-overflow-scrolling: touch;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100dvh;
  padding: 2rem;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  transform: translateY(-100%);
}

.mobile-menu.active {
  transform: translateY(0);
}

.mobile-menu .mobile-menu-header {
  width: 100%;
  padding-top: env(safe-area-inset-top, 1rem);
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  display: flex;
  position: relative;
}

.mobile-menu .mobile-menu-header .logo {
  color: #fff;
  background-color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  margin: 1rem auto 0;
  font-size: 2rem;
  font-weight: bold;
  display: flex;
}

.mobile-menu .mobile-menu-header .close-menu {
  cursor: pointer;
  width: 40px;
  height: 40px;
  right: 0;
  top: env(safe-area-inset-top, 1rem);
  background: none;
  border: none;
  position: absolute;
}

.mobile-menu .mobile-menu-header .close-menu span {
  background-color: var(--text-color);
  width: 100%;
  height: 2px;
  position: absolute;
  left: 0;
}

.mobile-menu .mobile-menu-header .close-menu span:first-child {
  top: 50%;
  transform: rotate(45deg);
}

.mobile-menu .mobile-menu-header .close-menu span:last-child {
  top: 50%;
  transform: rotate(-45deg);
}

.mobile-menu .mobile-nav {
  flex-direction: column;
  flex: 0 auto;
  justify-content: center;
  width: 100%;
  margin: auto 0;
  display: flex;
}

.mobile-menu .mobile-nav ul {
  flex-direction: column;
  gap: 3rem;
  padding: 0;
  list-style: none;
  display: flex;
}

.mobile-menu .mobile-nav li {
  margin: 0;
}

.mobile-menu .mobile-nav a {
  color: var(--text-color);
  letter-spacing: .05em;
  font-size: 2rem;
  font-weight: 500;
  text-decoration: none;
}

.mobile-menu .mobile-nav a:hover {
  color: var(--primary-color);
}

@media screen and (height <= 700px) {
  .mobile-menu .mobile-nav a {
    font-size: 1.5rem;
  }
}

.mobile-menu .mobile-social-links {
  width: 100%;
  padding: 2rem 0;
  padding-bottom: env(safe-area-inset-bottom, 2rem);
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
  display: flex;
}

.mobile-menu .mobile-social-links a, .mobile-menu .mobile-social-links button {
  color: var(--text-color);
  text-decoration: none;
  transition: color .3s;
}

.mobile-menu .mobile-social-links a:hover, .mobile-menu .mobile-social-links button:hover {
  color: var(--primary-color);
}

.mobile-menu .mobile-social-links .social-icon {
  object-fit: contain;
  width: 24px;
  height: 24px;
}

.mobile-menu .mobile-social-links button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

@media screen and (width >= 1280px) {
  .mobile-menu {
    display: none;
  }
}

.menu-toggle {
  cursor: pointer;
  z-index: 1000;
  background: none;
  border: none;
  width: 40px;
  height: 40px;
  padding: 0;
  display: none;
  position: relative;
}

.menu-toggle span {
  background-color: var(--text-color);
  width: 100%;
  height: 2px;
  margin: 8px 0;
  transition: all .3s;
  display: block;
}

@media screen and (width <= 1280px) {
  .menu-toggle {
    display: block;
  }
}

.desktop-nav {
  margin-right: auto;
  padding-left: 1rem;
  display: flex;
}

@media screen and (width <= 1280px) {
  .desktop-nav {
    display: none;
  }
}

.desktop-nav ul {
  align-items: center;
  gap: 3rem;
  list-style: none;
  display: flex;
}

header .logo {
  color: #fff;
  z-index: 1;
  cursor: pointer;
  background-color: #000;
  border-radius: 50%;
  justify-content: center;
