}

.mobile-menu.active ~ .menu-toggle {
  display: none;
}

@media screen and (width >= 1280px) {
  .menu-toggle, .mobile-menu {
    display: none !important;
  }
}

.project-card h3, .project-card a {
  color: var(--text-color);
}

.project-card a:hover {
  color: var(--primary-color);
}

footer {
  text-align: center;
